// src/components/globals/TrackGclid.tsx
import { useEffect } from 'react';

export default function TrackGclid() {
  useEffect(() => {
    if (typeof window === 'undefined') return;

    const params = new URLSearchParams(window.location.search);
    const gclid = params.get('gclid');
    const now = Date.now();

    const existing = localStorage.getItem('gclid_data');
    let data = existing ? JSON.parse(existing) : null;

    if (gclid) {
      if (!data || data.gclid !== gclid) {
        data = {
          gclid,
          first_visit: now,
          visit_count: 1,
          last_visit: now,
          events: []
        };
      } else {
        data.visit_count += 1;
        data.last_visit = now;
      }

      localStorage.setItem('gclid_data', JSON.stringify(data));
    }
  }, []);

  return null;
}
