// src/components/Clarity.tsx
import { useEffect } from 'react';
import {clarity} from 'react-microsoft-clarity';

type ClarityProps = {
  id: string;
};

export default function Clarity({ id }: ClarityProps) {
  useEffect(() => {
       //@ts-ignore
    if (typeof window !== 'undefined' && !window.__clarity_loaded) {

        //@ts-ignore
        (window).__clarity_loaded = true;
        
        //@ts-ignore
        clarity.init(id);
    }
  }, [id]); // ✅ include id in dependencies

  return null;
}
