import React, { useState, useEffect } from 'react';
import {
  FaBars,
  FaTimes,
  FaChevronRight,
  FaChevronDown
} from 'react-icons/fa';
import './nav.css';
import { StaticImage } from 'gatsby-plugin-image';
import { MantineProvider } from '@mantine/core';
import '@mantine/core/styles.css';
import Rating from '../testimonial/rating';
import { trackEvent } from '../globals/Gclid'; // ✅ Add this import

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const [isServicesDropdownOpen, setIsServicesDropdownOpen] = useState(false);
  const [isMobileServicesDropdownOpen, setIsMobileServicesDropdownOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
    setIsMobileServicesDropdownOpen(false);
  };

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 20);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const serviceLinks = [
    { title: 'Carpet', link: '/carpet-installation-boise/' },
    { title: 'Hardwood', link: '/hardwood-flooring-boise/' },
    { title: 'Laminate', link: '/laminate-flooring-boise/' },
    { title: 'Luxury Vinyl Plank', link: '/lvp-flooring-boise/' },
    { title: 'Hardwood Refinishing', link: '/hardwood-refinishing-boise/' },
    { title: 'Area Rugs', link: '/area-rugs-boise/' },
    { title: 'Tile', link: '/tile-flooring-boise/' }
  ];

  return (
    <>
      <header className={`header ${scrolled ? 'scrolled' : ''}`}>
        <div className="container">
          <nav className="nav-links">
            <a href="/#" className="nav-link">Home</a>

            <div
              className={`nav-link dropdown ${isServicesDropdownOpen ? 'open' : ''}`}
              onMouseEnter={() => setIsServicesDropdownOpen(true)}
              onMouseLeave={() => setIsServicesDropdownOpen(false)}
            >
              <button className="dropdown-toggle">
                Services
                <FaChevronDown className={`dropdown-icon ${isServicesDropdownOpen ? 'rotate' : ''}`} />
              </button>

              {isServicesDropdownOpen && (
                <div className="dropdown-menu">
                  {serviceLinks.map((service, index) => (
                    <a
                      key={index}
                      href={service.link}
                      className="dropdown-item"
                      onClick={() => {
                        trackEvent("none"); // ✅ tracking
                        setIsServicesDropdownOpen(false);
                      }}
                    >
                      {service.title}
                    </a>
                  ))}
                </div>
              )}
            </div>

            <a href="/#gallery" className="nav-link">Gallery</a>
            <a href="/#testimonial" className="nav-link">Testimonials</a>
          </nav>

          <a href="/" className="logo">
            <div className="logo-container1">
              <StaticImage
                src="../../images/logo.png"
                alt="Ideal Floors logo"
                className="logo1"
              />
            </div>
          </a>

          <div className="button-container">
            <a href="/contact">
              <button
                className="action-button"
                onClick={() => trackEvent("form")} // ✅ tracking
              >
                Contact Us
                <FaChevronRight className="button-icon" />
              </button>
            </a>
          </div>

          <button className="mobile-menu-button-q" onClick={toggleMenu}>
            {isMenuOpen ? <FaTimes size="1.7rem" /> : <FaBars size="1.7rem" />}
          </button>
        </div>
      </header>

      {isMenuOpen && (
        <div className="mobile-menu-overlay" onClick={toggleMenu}></div>
      )}

      <div className={`mobile-menu ${isMenuOpen ? 'open' : ''}`}>
        <div className="mobile-menu-header">
          <div className="mobile-logo">
            <StaticImage
              src="../../images/logo.png"
              alt="Ideal Floors logo"
              className="mobile-logo-img"
            />
          </div>
          <button className="mobile-close-button" onClick={toggleMenu}>
            <FaTimes />
          </button>
        </div>

        <div className="mobile-links">
          <a href="/" className="mobile-link" onClick={toggleMenu}>
            <span>Home</span>
          </a>

          <div className="mobile-dropdown">
            <button
              className="mobile-dropdown-toggle"
              onClick={() => setIsMobileServicesDropdownOpen(!isMobileServicesDropdownOpen)}
            >
              <span>Services</span>
              <FaChevronDown className={`mobile-dropdown-icon ${isMobileServicesDropdownOpen ? 'rotate' : ''}`} />
            </button>

            <div className={`mobile-dropdown-menu ${isMobileServicesDropdownOpen ? 'open' : ''}`}>
              {serviceLinks.map((service, index) => (
                <a
                  key={index}
                  href={service.link}
                  className="mobile-dropdown-item"
                  onClick={() => {
                    trackEvent("none"); // ✅ tracking
                    toggleMenu();
                  }}
                >
                  {service.title}
                </a>
              ))}
            </div>
          </div>

          <a href="/#gallery" className="mobile-link" onClick={toggleMenu}>
            <span>Gallery</span>
          </a>
          <a href="/#testimonial" className="mobile-link" onClick={toggleMenu}>
            <span>Testimonials</span>
          </a>
        </div>

        <a href="/contact" className="mobile-button-container">
          <button
            className="mobile-button"
            onClick={() => {
              trackEvent("form"); // ✅ tracking
              toggleMenu();
            }}
          >
            Contact Us
          </button>
        </a>
      </div>
    </>
  );
};

export default Header;
